let MathUtil = {
    // [min, max]
    randomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
    },

    // [min, max]
    randomFloat(min, max) {
        return Math.random() * (max - min + 1) + min;
    },

    // 
    prefixZero(num, n) {
        return (Array(n).join(0) + num).slice(-n);
    }
}

module.exports = MathUtil;
