// 

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this._eventMap = {};
    },

    //
    register(name, callfunc) {
        this._eventMap[name] = callfunc;
    },

    //
    unregister(name) {
        if(this._eventMap[name]) {
            delete this._eventMap[name];
        }
    },

    //
    emit(name, params) {
        let callfunc = this._eventMap[name];
        if(callfunc) {
            callfunc(params);
        } else {
            cc.warn(`${name}`);
        }
    },

    //
    once(name, params) {
        this.emit(name, params);
        this.unregister(name);
    },

    //
    clear() {
        this._eventMap = {};
    }
});
