
cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    init () {

    },

    //
    register(params) {
        Wu57.Bomb.User.register(params).then(res => {
            console.log(': ', res);
            Wu57.NoticeMgr.sendTip('');
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.RegSuccess, res);
            this.login(params);
        }).catch(err => {
            console.log(': ', err);
            if(err.code == 201) {
                Wu57.NoticeMgr.sendError('');
            } else if(err.code == 202) {
                Wu57.NoticeMgr.sendError('');
            } else if(err.code == 203) {
                Wu57.NoticeMgr.sendError('');
            } else if(err.code == 301) {
                Wu57.NoticeMgr.sendError('');
            } else {
                Wu57.NoticeMgr.sendError('');
            }
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.RegFailed, err);
        });
    },

    //
    login(params, callback) {
        let onComplete = function() {
            if(callback) {
                callback();
            }
        };

        Wu57.Bomb.User.login(params.username, params.password).then(res => {
            console.log('[]');
            let createdAt = (new Date(res.createdAt)).getTime();
            let updatedAt = (new Date(res.updatedAt)).getTime();
            if(createdAt === updatedAt) {
                console.log('[]');
            } else {
                console.log('[]');
                onComplete();
            }
            
            this.scheduleOnce( () => {
                Wu57.NoticeMgr.sendTip('');
                Wu57.DatabaseMgr.init(res.objectId, params.password);
            }, 1);
            Wu57.StorageMgr._setSession(params);
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.LoginSuccess, res);
        }).catch(err => {
            console.log('[]', err);
            if(err.code == 101) {
                Wu57.NoticeMgr.sendError('');
            } else {
                Wu57.NoticeMgr.sendError('');
            }
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.LoginFailed, err);
        });
    },

    //
    resetPassword(params) {
        Bmob.requestPasswordReset(params).then(res => {
            console.log('[]', res);
            Wu57.NoticeMgr.sendTip('\n');
        }).catch(err => {
            console.log('[]', err);
            Wu57.NoticeMgr.sendError(err.error);
        });
    }
});
