const AudioName = {
    Click: 'Click',
    Complete: 'Complete',
    Dodge: 'Dodge',
    Enter: 'Enter',
    Heal: 'Heal',
    LevelUp: 'LevelUp',
    Revive: 'Revive',
    MainBg1: 'MainBg1',
    MainBg2: 'MainBg2',
    PlayBg1: 'PlayBg1',
    PlayBg2: 'PlayBg2',
    Hammer: 'Hammer',
    Win: 'Win',
    Fail: 'Fail',
};

cc.Class({
    extends: cc.Component,

    properties: {
        AudioContainer: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this._music = null;
        this._mute = false;
        this._volume = 1;
        this.AudioName = AudioName;
        this.initAudios();
    },

    //
    initAudios() {
        this._audioList = {};
        this._audioComs = this.AudioContainer.getComponents(cc.AudioSource);
        for(let i=0; i<this._audioComs.length; i++) {
            let source = this._audioComs[i];
            this._audioList[source.clip.name] = source;
        }
    },

    //
    apply(key, val) {
        for(let i=0; i<this._audioComs.length; i++) {
            let source = this._audioComs[i];
            source[key] = val;
        }
    },

    //
    getAudioSource(name) {
        return this._audioList[name];
    },

    //
    setVolumn(vol) {
        if(vol != this._volume) {
            this._volume = vol;
            this.apply('volume', vol);
        }
    },

    //
    getVolumn() {
        return this._volume;
    },

    //
    enableMute() {
        if(!this._mute) {
            this._mute = true;
            this.apply('mute', true);
            Wu57.StorageMgr.setMusicOpen(false);
        }
    },

    //
    disableMute() {
        if(this._mute) {
            this._mute = false;
            this.apply('mute', false);
            Wu57.StorageMgr.setMusicOpen(true);
        }
    },

    //
    play (name) {
        if(this._last) {
            this._last.stop();
        }

        let source = this.getAudioSource(name);
        if(source) {
            if(source.loop && this._music) {
                //
                this._music.stop();
            }
            if(source.loop) {
                this._music = source;
            } else {
                this._last = source;
            }
            source.play();
        }
    },

    //
    stop (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.stop();
        }
        if(!source.loop) {
            this._last = null;
        }
    },

    //
    pause (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.pause();
        }
    },

    //
    resume (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.resume();
        }
    },

    //
    rewind (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.rewind();
        }
    },

    //
    pauseMusic() {
        if(this._music) {
            this._music.stop();
            console.log('[]');
        }
        if(this._last) {
            this._last.stop();
            this._last = null;
        }
    },

    //
    resumeMusic() {
        console.log('[]');
        if(this._music) {
            this._music.play();
        }
    }
});
